import image from './wink.png';
import birtdayImage from './birtday.png';
import birtdayGif from './birtday.gif';
import birtdayText from './birtday_text.png';
import './App.css';
import { Fireworks } from 'fireworks/lib/react'
import {useState }from "react"
import Countdown from 'react-countdown';
import ReactCardFlip from 'react-card-flip';

const renderer = ({ days, hours, minutes, seconds, completed }) => {
  if (completed) {
    // Render a completed state
    console.log("completed")
    return ""
  } else {
    // Render a countdown
    console.log("not completed")
    return (
    <div>
      <h2>{days} days, {hours} hours,</h2>
      <h2>{minutes} minutes and {seconds} seconds</h2>
    </div>
    )
  }
};

const Completionist = () => <span>HAPPY BIRTHDAY</span>;


const App = () => {
    const [flipped, setFlipped] = useState(false)
    const [complete, setComplete] = useState(Date.now() > new Date("February 27, 2023 06:00:00"))
    console.log("complete: ", complete)

    const handleClick = () => {
        setFlipped(!flipped);
    }

    const completeCountdown = () => {
      console.log("callback")
      setComplete(true);
    }

    return (
      <div className="App">
        <header className="App-header">
          {complete == false ?
            <>
              <img src={image} className="App-logo" alt="image" />
              <Countdown
                date={new Date("February 27, 2023 06:00:00")}
                //date={Date.now() + 3000}
                onComplete={completeCountdown}
                renderer={renderer}/>

              <ReactCardFlip isFlipped={flipped} flipDirection="horizontal">
                <div className="flippCard" onClick={handleClick}>
                  Click me!
                </div>

                <div className="flippCard" onClick={handleClick}>
                  "Have a great day!"
                </div>
              </ReactCardFlip>
          </>
            :
              <>
                <img src={birtdayGif} className="App-logo" alt="image" />
              </>
          }
        </header>
      </div>
    );
}

export default App;
